body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.resources {
    background-color: #84603e;
    right: 0;
    top: 0;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
}

.resources__resource {
    font-weight: 700;
    padding: 10px;
    color: #fcc352;
    border-left: solid 2px #4f271b;
    border-right: solid 2px #4f271b;
    border-top: solid 4px #4f271b;
    border-bottom: solid 4px #4f271b;
}

.resources__food {
    flex: 1 1;
    font-size: calc(1vw + 1vh);
}

.resources__population {
    flex: 1 1;
    font-size: calc(1vw + 1vh);
}

.resources__money {
    flex: 1 1;
    font-size: calc(1vw + 1vh);
}

.resources__date {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    flex: 3 1;
    font-size: calc(1.2vw + 1.2vh);
}

.resources__date--button {
    flex: 1 1;
    background-color: #4f271b;
    color: #fcc352;
    border: #4f271b;
}

.resources__date--value {
    flex: 1 1;
}

.main {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
}

.buildingsList {
    flex: 1 1;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    background-color: #84603e;
    min-height: 90vh;
    width: calc(6vw + 6vh);
}

.building {
    margin-bottom: 5vh;
    text-align: center;
    color: #fcc352;
    border-bottom: solid 4px #4f271b;
}

.building__image {
    -webkit-user-select: none;
    -ms-user-select: none;
        user-select: none;
    -webkit-user-drag: none;
    width: calc(6vw + 6vh);
    height: auto;
    cursor: pointer;
}

.building__name {
    font-size: calc(1.2vw + 1.2vh);
}

.board {
    flex: 10 1;
}

.buildArea {

}

.buildArea__image {
    margin: 1%;
    width: calc(6vw + 6vh);
    height: auto;
    -webkit-user-select: none;
    -ms-user-select: none;
        user-select: none;
    -webkit-user-drag: none;
}
